import React, { useEffect, useState } from 'react'
import { Box, Button, Input, Text, VStack, Tooltip } from '@chakra-ui/react'
import Lottie from 'react-lottie-player'
import Container from '../../../layouts/Container'
import loadingb2f5ea from '../../../animations/loadingb2f5ea.json'
import useAuthStore from '../../../store/useAuthStore'

const Search = () => {
  const { getDataViolitySearching } = useAuthStore()
  const [searchTerm, setSearchTerm] = useState('')
  const [lastSearchTerm, setLastSearchTerm] = useState('')
  const [searchResult, setSearchResult] = useState(null)
  const [isSearching, setIsSearching] = useState(false)
  const [isRepeat, setIsRepeat] = useState(false)

  const handleSearch = () => {
    if (isSearching || searchTerm.trim() === lastSearchTerm.trim()) return
    setIsSearching(true)
    setLastSearchTerm(searchTerm.trim())
    if (searchTerm.trim()) {
      getDataViolitySearching(searchTerm)
        .then((response) => {
          setSearchResult(response)
        })
        .finally(() => setIsSearching(false))
    }
  }

  const handleKeyDown = (e) => {
    if (e.key === 'Enter' && !isSearching) {
      handleSearch()
    }
  }

  useEffect(() => {
    setIsRepeat(searchTerm.trim() === lastSearchTerm.trim() || isSearching)
  }, [searchTerm, lastSearchTerm])

  return (
    <Container>
      <VStack spacing={4}>
        <Input
          type="text"
          value={searchTerm}
          onKeyDown={handleKeyDown}
          onChange={(e) => setSearchTerm(e.target.value)}
          placeholder="Enter search term"
          variant="filled"
          focusBorderColor="blue.500"
          ref={() => {}}
        />
        <Tooltip
          isDisabled={!isRepeat || isSearching}
          hasArrow
          label={
            searchTerm.trim() ? 'You have got a list for this word!' : 'You can type any words in the field, please'
          }
          bg="yellow.600"
          placement="auto-start"
        >
          <Button
            leftIcon={
              isSearching && (
                <Lottie
                  loop
                  animationData={loadingb2f5ea}
                  color="turquoise"
                  play={isSearching}
                  style={{ width: 50, height: 50 }}
                />
              )
            }
            isDisabled={isRepeat && !isSearching}
            colorScheme="teal"
            onClick={handleSearch}
          >
            <Text color={isRepeat ? 'red.100' : 'teal.100'}>Search</Text>
          </Button>
        </Tooltip>
      </VStack>

      {/* Search Results */}
      <Box mt={8} p={4} borderWidth="1px" borderRadius="md">
        {searchResult && searchResult.length > 0 ? (
          searchResult.map((item, index) => (
            <Box key={item.id} mb={4} p={3} borderBottomWidth={index < searchResult.length - 1 ? '1px' : '0'}>
              <Text fontWeight="bold" mb={2}>
                {item.title}
              </Text>
              <Text>Price: {item.prices}</Text>
              <Text>Ending Time: {item.endingTime}</Text>
            </Box>
          ))
        ) : (
          <Text>No results yet</Text>
        )}
      </Box>
    </Container>
  )
}

export default Search
